 import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
export default {
	data() {
		return {
			src: ''
		}				
	},
	methods:{
		upload() {
			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success(res) {
					const src = res.tempFilePaths[0];
		
					uni.redirectTo({
						url: 'userpicupload?src=' + src
					});
				}
			});
		},
	},
    computed: mapState(['hasLogin','userInfo']), 
	onLoad(option) { 
		let { avatar } = option;
		if (!this.hasLogin) {  
			uni.navigateTo({
				url:'../login',
			})
		}
		if (avatar) {
			this.src=avatar;
		}
	}
};
